#nav-bar {
    position: fixed;
    z-index: 9999;
    border-bottom-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
    width: 100%;
    background-image: linear-gradient(to bottom, rgba(100, 100, 100, 0.3), rgba(100, 100, 100, 0.1));
    backdrop-filter: blur(3px);
}

#brand-is-left {
    max-height: 4rem;
    margin-left: 1rem;
}

#register-button button {
    color: white;
    background-color: #294692;
    border: none;
    position: absolute;
    right: 1.3rem;
    top: 1.1rem;
}

#register-icon {
    width: 20px;
    margin-left: 0.4rem;
    filter: brightness(0) invert(1);
}