.pre-loader {
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: 999999;
    background-color: #f5f5f5;
    background-image: url("/JEEC-Version-2/imgs/bg-colour.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    opacity: 0.9;
    backdrop-filter: blur(3px);
}

.main-logo {
    margin: 0;
    padding: 0;
    position: absolute;
    max-width: 12rem;
    top: 25%;
    left: calc(50% - 6.5rem);
}

.loading-img {
    margin: 0;
    padding: 0;
    position: absolute;
    bottom: 3rem;
    left: calc(50% - 1.2rem);
    max-width: 2.4rem;
}